import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage-angular';
import { BehaviorSubject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Help } from '../models/Help';
import { map } from 'rxjs/operators';
import { StorageService } from './storage.service';
import { FormService } from './form.service';
import { ToastService } from './toast.service';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  public user = new BehaviorSubject<any>(null);
  public optionChosed: BehaviorSubject<any> = new BehaviorSubject(null);
  private url = environment.url;
  constructor(public  http: HttpClient,
    private storageService: StorageService,
    private formService: FormService,
    private toastService: ToastService,
    private router: Router) {

  }
   async loadUser(){
   await this.storageService.getStorage2('user').then(
    res =>{
    console.log(res);
    }
   );

}

async setUser(user: any){
    const id = await this.storageService.getStorage2('id');
     const b = await this.storageService.saveStorage('user',user);
     await this.formService.update(user,'user');
     try{
     const response: any = await this.http.post(`${this.url}/help/${id}`,user).toPromise();
     if(response.status){
      this.storageService.saveStorage('user_update',true);
      this.user.next(user);
      this.toastService.toast('Datos guardados ! :)','success');
      return Promise.resolve(true);
      }
    }
    catch{
      this.storageService.saveStorage('user_update',false);
      this.toastService.toast('Hubo un error al cargar tus datos :( ','danger');
      return Promise.resolve(false);
    }
}



async getUser(): Promise<boolean>{
let isData: boolean;
  await this.storageService.hasValue('user').then(
    res=>{
        isData = res;
    }
  );
    if(isData){
        this.user.next(await  this.storageService.getStorage2('user'));
        return Promise.resolve(true);
    }
    else{
       return Promise.resolve(false);
    }
}

 getData(){
    return this.user.asObservable();
}

 clearData(){
   this.storageService.clearData();
   this.router.navigate(['onboarding'],{replaceUrl: true});
}
}
