/* eslint-disable @typescript-eslint/dot-notation */
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { FormService } from '../services/form.service';
import { Subscription } from 'rxjs';
import { UserService } from '../services/user.service';
import { ToastService } from '../services/toast.service';
import { ModalController, PopoverController, AlertController } from '@ionic/angular';
import { SobreNosotrosPage } from '../sobre-nosotros/sobre-nosotros.page';
import { Route, Router } from '@angular/router';
@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.page.html',
  styleUrls: ['./perfil.page.scss'],
})

export class PerfilPage implements OnInit {
  myForm: FormGroup;
  title: string;
  $obs = new  Subscription();
  loaded: boolean;

  constructor(private formBuilder: FormBuilder,private formService: FormService,
    private userService: UserService,
    private toastService: ToastService,
    private modalCtrl: ModalController,
    private alertController: AlertController,
    private router: Router
     ) { }
  async ngOnInit() {
    this.myForm = this.formBuilder.group({
      name: ['', [Validators.required, Validators.minLength(3)]],
      phone: ['', [Validators.required, Validators.minLength(3)]],
      email: ['', [Validators.required,]],
      address: ['',[Validators.required]],
    });
    this.loaded = await this.userService.getUser();
    if(this.loaded){
        const sub =  this.userService.getData().subscribe( res=> {this.patchForm(res);});
        this.$obs.add(sub);
    }
  }

  async onSubmit(){
    const a = await this.userService.setUser(this.myForm.value);
   if(a){
    this.title = this.myForm.controls['name'].value;
    this.loaded = true;
   }
   else {
    this.loaded = true;
   }

  }


    ionViewWillLeave(){
      this.$obs.unsubscribe();
    }
 patchForm(res: any){
    this.myForm.patchValue({
         name: res.name,
         phone: res.phone,
         email: res.email,
         address: res.address
    });
    this.title = res.name;
  }

  async modalAboutUs(){
    const modal = await this.modalCtrl.create({
      component: SobreNosotrosPage,
    });
    modal.present();
    await modal.onWillDismiss();
  }

  async changeForm(){
    const alert = await this.alertController.create({
      header: 'Estás seguro?',
      buttons: [
        {
          text: 'No',
          cssClass: 'alert-button-cancel',
        },
        {
          text: 'Si',
          cssClass: 'alert-button-confirm',
          handler: () => {
            this.userService.clearData();
          },
        },
      ],
    });

    await alert.present();
  }

}


